.card{
  &-grid{
    &.material-grid{
      .card-item__title{
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
      }
    }
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      gap: 40px 25px;
      &_small{
        gap: 15px;
      }
    }
    &__item{
      &_column{
        &-medium{
          grid-column-end: span 2;
        }
      }
    }
    &__footer{
      margin-top: 35px;

      display: flex;
        align-items: flex-end;
      justify-content: flex-end;

      flex-direction: column;
      &_margin{
        &-small{
          margin-top: 15px;
        }
      }
      &_position{
        &-center{
          justify-content: center;
        }
      }
      &-img{
        width: 100%;
      }
    }
    &__link{
      font-family: 'Montserrat', sans-serif;
      font-size: 14px;
      font-weight: 500;
      letter-spacing: -0.01em;

      color: #1b1b1b;
    }
    &__btn{
      max-width: 195px;
      height: 45px;
      &-mobile{
        display: none;
      }
    }
  }
  &-item{
    height: 100%;
    &__wrapper{
      position: relative;

      background: #fff;
      border-radius: 8px;

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      &_row{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;

        padding: 15px;

        height: 100%;
      }
    }
    &__header{
      display: flex;
      flex-direction: column;
      justify-content: center;


      position: relative;

      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
      overflow: hidden;
      &_size{
        &-small{

          height: auto;
          flex-shrink: 0;
        }
      }
      &-stars{
        position: absolute;
        left: 9px;
        top: 7px;
      }
      &-footer{
        position: relative;
        left: 0;
        bottom: 0;

        display: flex;
        align-items: center;
        justify-content: flex-start;

        padding: 10px;
      }
    }
    &__stars{
      display: grid;
      grid-template-columns: repeat(5,minmax(0,12px));
      gap: 1px;
    }
    &__star{
      width: 12px;
      height: 12px;
      &-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__category{
      width: 20px;
      height: 20px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-icon{
        width: 18px;
        height: 18px;
      }
    }
    &__date{
      margin-left: auto;

      font-family: 'Montserrat', sans-serif;
      font-size: 12px;
      font-weight: 500;
      color:#6a6a6a;
    }
    &__tags{
      display: flex;
      align-items: center;
      margin-left: 4px;
    }
    &__tag + &__tag{
      margin-left: 4px;
    }
    &__tag{
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      height: auto;
      flex-wrap: wrap;


      border-radius: 10px;

      margin-left: 10px;

      &-item{
        display: flex;
        align-items: center;
        justify-content: center;

        padding: 0 10px;
        height: 20px;

        margin:2.5px;

        border-radius: 4px;
        border: 1px solid #6a6a6a;
      }
      &-text{
        font-family: 'Montserrat', sans-serif;
        font-size: 10px;
        font-weight: 600;
        letter-spacing: -0.01em;

        color: #4F4F4F;
      }
    }
    &__img{
      width: 60px;
      height: 60px;

      flex-shrink: 0;
      object-fit: contain;
      &_size{
        &-full{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    &__content{
      padding: 0px 15px 20px 15px;
      &_size{
        &-small{
          margin-left: 15px;

          padding: 0;

          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 100%;
        }
      }
    }
    &__title{
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
      color:#2A2A2A;
      font-size: 15px;
      line-height: 1.2;
      &_position{
        &-center{
          text-align: center;
        }
      }
    }
    &__text{
      font-family: 'Montserrat', sans-serif;
      color:#858585;
      font-size: 11px;

      margin-top: 5px;

    }
    &__btn{
      position: absolute;
      bottom: -17.5px;
      left: calc(50% - 67.5px);
      width: 135px;
      height: 35px;
    }
  }
}

@media screen and (max-width: 1400px) {

}

@media screen and (max-width: 1400px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(3,minmax(0,1fr));
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(2,minmax(0,1fr));
        &_small{
          gap: 20px;
        }
      }
      &__btn{
        &-mobile{
          display: flex;
        }
      }
    }
    &-item{
      &__title{
        text-align: left;

        font-size: 16px;
      }
      &__text{
        font-size: 13px;
      }
      &__content{
        &_size{
          &-small{
            padding: 10px 0;
          }
        }
      }
      &__img{
        &_size-full{
          height: 180px;
        }
      }
    }
  }
  .card{
    &-item{
      &__category{
        width: 23px;
        height: 23px;
       &-icon{
         height: 23px;
         width: 23px;

         flex-shrink: 0;
       }
      }
    }
  }
}


@media screen and (max-width: 475px) {
  .test{
    &-grid{
      .card{
        &-grid{
          &__item{
            display: none;
            &:nth-child(1),&:nth-child(2),&:nth-child(3){
              display: block;
            }
          }
        }
      }
    }
  }
  .card {
    &-grid {
      &.material-grid{
        .card-item__title{
          font-size: 19px;
        }
      }
      &__wrapper {
        grid-template-columns: minmax(0,1fr);

        gap: 32px;
        &_small{
          gap: 15px;
        }
      }
      &__footer{
        &-img{
          margin-top: 20px;
        }
      }
    }
  }
}
