.banner{

  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 160px;
    padding: 0 60px 0 0;

    background: #FFFFFF;
    border-radius: 8px;
    &-mobile{
      display: none;
    }
  }
  &__img{
    max-width: 330px;
    max-height: 180px;
    flex-shrink: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    img{
      height: auto;

      max-height: 200px;

      object-fit: contain;
    }
  }
  &__content{
    max-width: 550px;

    margin: 0 15px;
  }
  &__title{
    font-family:'Montserrat', sans-serif;
    font-weight: 400;

    font-size: 31px;
    line-height: 1.2;

    color: #23222F;
  }
  &__text{
    margin-top: 4px;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    line-height: 115%;

    letter-spacing: -0.01em;

    color: #23222F;
  }
  &__footer{
    margin-left: auto;
  }
  &__btn{
    width: 150px;
    height: 42px;

    padding: 0 15px;

    border: 1px solid #23222F;
    border-radius: 10px;


    &:hover{
      box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
    }
  }
}

@media screen and (max-width: 860px) {
  .banner{
    &__wrapper{
      flex-wrap: wrap;
      justify-content: flex-start;
      height: auto;

      padding: 0px 15px 30px 30px;
    }
    &__content{
      max-width: calc(100% - 80px);
      margin: 0;
    }
    &__btn{
      width: 75px;
    }
    &__img{
      width: 100%;
      height: 80px;

      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      transform: translateY(-20px);
      img{
        width: auto;
        height: 120%;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .banner{
    &__wrapper{
      padding: 10px;

      background-color: #ba90ff;
      &-descktop{
        display: none;
      }
      &-mobile{
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-start;

        padding-left: 20px;
        padding-right: 15px;

        .banner__desc{
          width: 100%;
          text-align: center;
        }
      }
    }
    &__img{
      height: 50px;
      width: 50px;

      transform: translateY(0);
      img{
        width: 50px;
        height: 50px;
      }
    }
    &__text{
      font-family: 'Montserrat', sans-serif;
      color:#fff;

      font-size: 18px;
      font-weight: 600;
    }
    &__desc{
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 600px) {
  .banner{
    margin: 20px 0 35px 0;
    &__text{
      margin-top: 0;
    }
  }
}